import React from "react";
import "./About.css";
import Title from "../title/Title";

export default function About() {
  return (
    <div className="container my-5" id="about">
      <Title title="ber mich" />

      <div className="container">
        <p>Ich bin in Kamerun geboren.</p>
        <p>
          Nach meinem Abitur habe ich in einer Firma gearbeitet, und meine
          Arbeit bestand darin, Wartungsarbeiten durchzufhren, das
          Betriebssystem von Computern zu ersetzen und beschdigte Teile eines
          Computers auszutauschen.
        </p>
        <p>
          Dann kam ich nach Deutschland, wo ich mich fr das Programmieren
          interessierte. Durch das Anschauen von Videos auf YouTube und Udemy
          begann ich, Design zu lernen (Photoshop, Illustrator, Adobe Premiere
          Pro, Adobe audition).
        </p>
        <p>
          Dann sah ich, dass sich mit der Erstellung von Internetseiten ein
          bisschen Geld verdienen lsst, begann Wordpress zu lernen und begann,
          Internetseiten fr Unternehmen in Afrika zu erstellen.
        </p>
        <p>
          Als ich mit dem Studium an der THM begann, lernte ich das Codieren und
          heute versuche ich, mich zu verbessern, indem ich jeden Tag etwas
          Neues programmiere.
        </p>
        <p>
          Aufgrund meiner derzeitigen Arbeit, die krperlich anstrengend ist,
          habe ich beschlossen, neben meinem Studium noch mehr Zeit in das
          Programmieren zu investieren, um meine Fhigkeiten zu verbessern und
          meine Chancen auf einen Werkstudenten zu erhhen.
        </p>
      </div>
    </div>
  );
}
